.auth-container {
  display: flex;
  flex-direction: column;
  gap: 24px;

  align-items: center;
  padding: 24px 0 40px;
}

.auth-header {
  font-family: Poppins;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0em;
  text-align: left;
  width: 300px;
  margin-bottom: 16px;
}

.auth-content {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}

.auth-step {
  margin: 10px 0;
}

.auth-step-button {
  color: #000000;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
}

.auth-step-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.main-auth-container {
  text-align: center;
  padding: 20px;
}

.main-auth-title {
  font-family: Poppins;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
  text-align: left;
  color: #b6b6b6;
  width: 300px;
  margin-bottom: 24px;
}

.auth-button {
  width: 142px;
  height: 36px;
  padding: 2px 10px 2px 10px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.42;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #e3ffa8;
  color: #000000;
}

/* .auth-button:focus {
  background-color: #e3ffa8;
  color: black;
  outline: none;
} */

.auth-button-su {
  width: 122px;
  height: 36px;
  padding: 2px 10px 2px 10px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.42;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: white;
}



.main-auth-button {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}

.auth-img {
  margin-bottom: 24px;
  width: 300px;
}

.auth-container ul li {
  position: relative;
  padding-left: 20px;
}

.auth-container ul li:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 8px;
  height: 8px;
  background-color: #e3ffa8;
  border-radius: 50%;
}

.auth-list-a-b {
  display: none;
}



.auth-list-item {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.42;

}

.auth-list-item:not(:last-of-type) {
  margin-bottom: 16px;
}

.googleAuth {
  width: 300px;
  height: 36px;
  display: flex;
  padding: 0.5rem 1.4rem;
  justify-content: center;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  vertical-align: middle;
  align-items: center;
  border-radius: 12px;

  gap: 0.75rem;

  color: #b6b6b6;
  cursor: pointer;
  transition: all 0.6s ease;
  margin: 0 auto 16px;
}

.googleAuth svg {
  height: 24px;
}

.googleAuth:hover {
  transform: scale(1.02);
}

@media (min-width: 834px) and (max-width: 1439.5px) {
  .auth-container {
    gap: 80px;
    padding: 40px 168px 100px;
  }

  .auth-list-c {
    display: none;
  }

  .auth-list-a-b {
    display: block;
    display: flex;
  }

  .main-auth-button {
    gap: 16px;
    margin-bottom: 16px;
  }

  .auth-content {
    max-width: 444px;
    align-items: center;
  }

  .auth-list-a {
    margin-right: 16px;
  }




  .auth-img {
    margin-bottom: 80px;
    width: 380px;
  }

  .main-auth-title {
    text-align: center;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.45;
    width: 100%;
    padding: 0 15px 0;

  }

  .auth-button {
    width: 212px;

  }



  .auth-header {
    width: auto;
    font-size: 30px;

    font-weight: 500;
    line-height: 1.2;

  }

  .googleAuth {
    margin-bottom: 80px;
  }
}

@media (min-width: 1440px) {
  .auth-container {
    display: flex;
    flex-direction: row;
    gap: 104px;
    justify-content: center;
    overflow: hidden;
    padding: 40px 116px 72px;


  }

  .googleAuth {
    margin: 0 25px 80px;

  }

  .auth-header {
    width: 424px;
    font-size: 30px;
    font-weight: 500;
    line-height: 1.3;
    text-align: left;
  }

  .auth-img {

    width: 592px;

  }

  .main-auth-button {
    gap: 16px;
    margin-bottom: 16px;
  }

  .main-auth-title {
    text-align: start;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.45;
    width: 100%;
    padding: 0;

  }

  .auth-button {
    width: 212px;

  }


  .auth-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

  }


}